import React, { useState, useEffect } from 'react';
import { BrowserRouter, Route, Routes, Navigate, useNavigate } from 'react-router-dom';
import Login from './components/Login';
import AdministracionProductos from './components/AdministracionProductos';
import ConsultaCotizaciones from './components/ConsultaCotizaciones';
import Logs from './components/Logs';

const Navbar = ({ isLoggedIn, handleLogout }) => {
  const navigate = useNavigate();

  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
      <div className="container-fluid">
        <a className="navbar-brand" href="/consulta-cotizaciones">COTIZADOR DUPAZA</a>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
            {isLoggedIn ? (
              <>
                <li className="nav-item">
                  <button className="btn btn-success" onClick={() => navigate('/administrar')}>
                    Administrar productos
                  </button>
                </li>
                <li className="nav-item">
                  <button className="btn btn-info" onClick={() => navigate('/logs')}>
                    Logs
                  </button>
                </li>
                <li className="nav-item">
                  <button type="button" className="btn btn-danger" onClick={handleLogout}>
                    Salir
                  </button>
                </li>
              </>
            ) : (
              <li className="nav-item">
                <button type="button" className="btn btn-primary" onClick={() => navigate('/login')}>
                  Iniciar sesin
                </button>
              </li>
            )}
          </ul>
        </div>
      </div>
    </nav>
  );
};

const App = () => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  useEffect(() => {
    const storedIsLoggedIn = localStorage.getItem('isLoggedIn');
    if (storedIsLoggedIn === 'true') {
      setIsLoggedIn(true);
    }
  }, []);

  const handleLogout = () => {
    localStorage.removeItem('isLoggedIn');
    setIsLoggedIn(false);
  };

  return (
    <BrowserRouter>
      <Navbar isLoggedIn={isLoggedIn} handleLogout={handleLogout} />
      <Routes>
        <Route path="/" element={<Navigate to="/consulta-cotizaciones" replace />} />
        <Route path="/consulta-cotizaciones" element={<ConsultaCotizaciones />} />
        <Route
          path="/logs"
          element={
            isLoggedIn ? <Logs /> : <Navigate to="/login" replace />
          }
        />
        <Route
          path="/administrar"
          element={
            isLoggedIn ? <AdministracionProductos /> : <Navigate to="/login" replace />
          }
        />
        <Route path="/login" element={<Login setIsLoggedIn={setIsLoggedIn} />} />
      </Routes>
    </BrowserRouter>
  );
};

export default App;